
import { mapState } from 'vuex'
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import event from '../../../js/Event.js'
import { getYMDFormateTime, isArrayType, getImageUrl } from "../../../js/utility.js"
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
name: 'tasklist',
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton
},
computed: {
...mapState([
'taskList'
]),
},
data() {
return {
p: 1,
nav_list: [],
nav_type: 1,
itemlist: []
}
},
mounted() {
event.$on('gotoAddData', function() {
this.$router.push({
name: 'taskhandle',
params: {
id: this.$route.params.id,
action: 'default'
}
});
}.bind(this));

if (this.taskList.permission_list) {
this.$store.commit('updateXheaderShowAdd', true);
}

this.nav_list = [{ name: '未完成', type: 1 }, { name: '已完成', type: 2 }];

this.nav_type = this.taskList.nav_type ? this.taskList.nav_type : 1;
this.handleCache(this.nav_type);

if (this.taskList.transfer) { //需要转移
this.taskList.transfer = false;
this.$store.commit('updateTaskList', this.taskList);
AjaxHttp('POST', 'm=task&a=order', { type_id: this.taskList.transferItem.id, task_id: this.taskList.currentItem.task_id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.itemlist.splice(wself.taskList.currentIndex, 1);
}
});
}



},
beforeDestroy() {
event.$off("gotoAddData");
},
beforeRouteLeave(to, from, next) {
if (to.name === 'project' && to.params.action === 'default') {
this.$store.commit('updateTaskList', {});
} else {
if (to.name === 'project' && to.params.action === 'select') {
event.$on('selectedRelateItem', function(data) {
this.handleRelateInfo(data);
}.bind(this));
}
this.saveCache(this.nav_type);
this.taskList.nav_type = this.nav_type;
this.$store.commit('updateTaskList', this.taskList);
}

next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'project' && from.params.action === 'select') {
event.$off("selectedRelateItem");
}
next();
},
methods: {
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var postParams = {};
postParams['p'] = this.p;
postParams['type_id'] = this.$route.params.id;
if (this.nav_type === 1) {
postParams['status'] = '未完成'; 
} else if (this.nav_type === 2) {
postParams['status'] = '完成'; 
}

AjaxSilentHttp('POST', 'm=task&a=index', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
if (res.permission_list.add === 1) {
wself.taskList.permission_list = true;
wself.$store.commit('updateXheaderShowAdd', true);
} else {
wself.taskList.permission_list = false;
wself.$store.commit('updateXheaderShowAdd', false);
}








if (wself.p === 1) {
wself.itemlist = res.list;
} else {
wself.itemlist = wself.itemlist.concat(res.list);
}

if (typeof done === 'function') {
if (wself.p === 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
}
});
},
clickNav(type) {
if (this.nav_type !== type) {
this.saveCache(this.nav_type); 
this.nav_type = type;
this.itemlist = [];
this.p = 1;
this.handleCache(this.nav_type); 
}
},
handleCache(nav_type) { //操作缓存
if (nav_type === 1 && this.taskList.unfinished) {
if (this.taskList.unfinished.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.taskList.unfinished.itemlist;
this.p = this.taskList.unfinished.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.taskList.unfinished.scroll, false);
if (this.taskList.unfinished.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}

} else if (nav_type === 2 && this.taskList.finished) {
if (this.taskList.finished.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.taskList.finished.itemlist;
this.p = this.taskList.finished.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.taskList.finished.scroll, false);
if (this.taskList.finished.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
} else {
this.$refs.my_scroller.finishInfinite(false);
}
},
saveCache(nav_type) {
var type = '';
if (nav_type === 1) {
type = 'unfinished'; 
} else if (nav_type === 2) {
type = 'finished'; 
}
this.taskList[type] = {
type: type, //平台类型与type相同 进行赋值
itemlist: this.itemlist,
p: this.p,
nav_type: this.nav_type,
refresh: false,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
};
},
enterTaskView(item) {
if (this.$route.params.action === 'default') {
this.$router.push({
name: 'taskview',
params: {
id: item.task_id
}
});
} else if (this.$route.params.action === 'select') {
event.$emit('selectedRelateItem', { type: 'tasklist', item: item });
this.$router.go(-1);
}
},
getTimeContent(item) {
return getYMDFormateTime(item.create_date);
},
getImgeSrc(item) {
return getImageUrl(item.thumb_path);
},
getStatusMark(item) {
if (item.status === '完成') {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
isCompleteStatus(item) {
if (item.status === '完成') {
return true;
}
return false;
},
priorityColor(item) {
if (item.priority === '高') {
return '#f96868';
} else if (item.priority === '普通') {
return '#46be8a';
} else if (item.priority === '低') {
return '#f2a654';
}
return '#FFF';
},
changeSatus(item, index) {
AjaxHttp('POST', 'm=task&a=edit', { id: item.task_id, status: item.status === '完成' ? '进行中' : '完成' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.itemlist.splice(index, 1);
}
});
},
onSwipeoutClick(item, type, index) {
this.taskList.currentItem = item;
this.taskList.currentIndex = index;
this.$store.commit('updateTaskList', this.taskList);
if (type === 'transfer') {
this.$router.push({
name: 'project',
params: {
action: 'select'
}
});
}
},
handleRelateInfo(data) {
if (data.type === 'project') {
this.taskList.transfer = true;
this.taskList.transferItem = data.item;
this.$store.commit('updateTaskList', this.taskList);
}
}
}
}




